*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-size: 1rem;
  color: #444444;
  font-family: "Helvetica Neue", Arial, "Hiragino Sans", Meiryo, sans-serif;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: #444444;
}

/*-------------------------------------------
content
-------------------------------------------*/
.content {
  display: flex;
  margin-bottom: 60px;
}
@media (max-width: 834px) {
  .content {
    flex-direction: column;
  }
}
.content .image {
  width: 50%;
}
@media (max-width: 834px) {
  .content .image {
    width: 100%;
  }
}
.content .image img {
  width: 100%;
  height: 700px;
  object-fit: cover;
  vertical-align: bottom;
}
@media (max-width: 834px) {
  .content .image img {
    height: auto;
  }
}
.content .recipe {
  width: 50%;
  padding: 40px 5% 0 5%;
}
@media (max-width: 834px) {
  .content .recipe {
    width: 100%;
  }
}
.content .recipe .page-title {
  font-size: 1.75rem;
  margin-bottom: 20px;
}
.content .recipe .content-title {
  font-size: 1.25rem;
  margin: 40px 0 15px 0;
  padding-bottom: 5px;
  border-bottom: 1px solid #444444;
}
.content .recipe .ingredient-list {
  display: flex;
  flex-wrap: wrap;
}
.content .recipe .ingredient-list dt {
  width: 85%;
  border-bottom: 1px dotted #444444;
  padding: 6px 0;
}
.content .recipe .ingredient-list dd {
  width: 15%;
  border-bottom: 1px dotted #444444;
  padding: 6px 0;
  text-align: right;
}
.content .recipe .step-list li {
  padding: 6px 0;
  border-bottom: 1px dotted #444444;
  margin-left: 20px;
}

/*-------------------------------------------
botton
-------------------------------------------*/
.botton {
  text-align: center;
  margin-bottom: 80px;
}
.botton .btn {
  text-decoration: none;
  display: inline-block;
  border: 1px solid #444444;
  padding: 18px 60px;
  font-size: 0.875rem;
}

/*-------------------------------------------
footer
-------------------------------------------*/
#footer {
  font-size: 0.75rem;
  text-align: center;
  padding: 20px;
}
#footer .sns {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
#footer .sns li {
  margin: 0 10px;
}